.visually-hidden{overflow:hidden;position:absolute;width:1px;height:1px;margin:-1px;padding:0;border:0;white-space:nowrap;clip-path:inset(50%)}*{margin:0;padding:0;font:inherit;color:inherit}*,:after,:before{box-sizing:border-box}:root{-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-text-size-adjust:100%;text-size-adjust:100%;cursor:default;line-height:1.5;overflow-wrap:break-word;word-break:break-word;tab-size:4}html,body{height:100%}body,input,textarea,select,button,table{font-family:-apple-system,BlinkMacSystemFont,"Malgun Gothic","맑은 고딕",helvetica,"Apple SD Gothic Neo",sans-serif}img,picture,video,canvas,svg{display:block;max-width:100%}input{margin:0;padding:0;border:0;-webkit-appearance:none;appearance:none}ol,ul{list-style:none}button{background:none;border:0;cursor:pointer}a{text-decoration:none}table{border-collapse:collapse;border-spacing:0}